// var socket = new SockJS(process.env.API + "/ticket-manager-service-0.0.1-SNAPSHOT/login-entries");
// var client = Stomp.over(socket);

// client.reconnect_delay = 5000;
// client.debug = function(str) {};

export default {
  name: "AgentLoginsByLocation",
  mounted() {
    this.refreshLogins();
    // this.connect();
  },
  data() {
    return {
      isBusy: false,
      isBusy2: false,
      logins: [],
      isConnected: false,
      count: 0
    };
  },
  methods: {
    refreshLogins: function() {
      var self = this;
      var d = [];

      this.isBusy = true;
      this.count = 0;
      
      this.$http
        .get(
          "/ticket-manager-service-0.0.1-SNAPSHOT/v1/loginUsers/countTotalLoggedInUsersForDayAndGroupByLocation"
        )
        .then(
          function(res) {
            this.isBusy = false;

            var H = highmaps,
              map = H.maps["countries/ng/ng-all"];

            d = [
              {
                lat: 6.5244,
                lon: 3.3792,
                name: "LAGOS",
                logins: res.body.LAGOS ? res.body.LAGOS : 0,
                z: res.body.LAGOS ? res.body.LAGOS : 0
              },
              {
                lat: 9.0765,
                lon: 7.3986,
                name: "ABUJA",
                logins: res.body.ABUJA ? res.body.ABUJA : 0,
                z: res.body.ABUJA ? res.body.ABUJA : 0
              }
            ];

            this.count = (d[0].logins + d[1].logins);

            highmaps.mapChart("logins", {
              title: {
                text: ""
              },
              series: [
                {
                  name: "Basemap",
                  mapData: map,
                  borderColor: "#606060",
                  nullColor: "rgba(200, 200, 200, 0.2)",
                  showInLegend: false
                },
                {
                  type: "mapbubble",
                  dataLabels: {
                    enabled: true,
                    format: "{point.name} - {point.logins}"
                  },
                  name: "Logins",
                  data: d,
                  maxSize: "12%",
                  color: H.getOptions().colors[0]
                }
              ]
            });

            self.isBusy = false;
          },
          function(err) {
            $.notify(
              {
                message:
                  "Ops! Something went wrong. Data for Logins chart failed to load."
              },
              {
                allow_dismiss: false,
                type: "danger",
                template:
                  "<div data-notify='container' class='col-xs-11 col-sm-3 callout callout-{0}'><p data-notify='message'>{2}</p></div>"
              }
            );
            self.isBusy = false;
          }
        );
    },
    connect: function() {
      client.connect({}, this.connectCallback, this.errorCallback);
    },
    disconnect: function() {
      if (stompClient !== null) {
        client.disconnect();
      }

      this.isConnected = false;
    },
    connectCallback: function(frame) {
      var self = this;

      client.subscribe("/topic/login-entrie", function(message) {
        var res = JSON.parse(message.body);
        console.log(res);
      });

      console.log("Connected");
      self.isConnected = true;
    },
    errorCallback: function(error) {
      this.isConnected = false;
      console.log("Ops! Something went wrong with your connection.");
    }
  }
};
</script>

